.biometrics-page{
    padding-top: 90px;
    overflow: hidden;
    .nav{
        background-color: #fff;
        border-bottom: 1px solid #ccc;

        .nav-logo{
            .logo-container{
                content: url('../../../../assets/Navbar/blue-logo.png') !important;
            }
            h6{
                color:#111 !important;
            }
        }
        .nav-links{
            .inner-nav-link{
                color:#000 !important;
                svg{
                    color:#444;
                }
            }
        }
        .nav-auth{
            .nav-login a{
                color:#000 !important;
            }
        }
    }
    .nav__sticky{
        border:0;
    }
    .mobile-toolbar{
        background-color: white;
        border-bottom: 1px solid #ccc;
        transition: 0.2s ease;
        .mobile-logo-row{
            h6{
                color:#333 !important;
            }
        }
        button{
            color:#222 !important;
        }
    }
    .toolbar__sticky{
        border: 0;
    }
}

.biometrics-page .shared-banner{
    clip-path: polygon(0 0, 100% 0, 100% 100%, 100% 100%, 65% 80%, 0 100%);
    position: relative;
    overflow:visible;
    z-index: 1;
    padding:10% 8%;
    h1{
        font-size: 42px;
    }
    .banner-img{
        background-color: #fff;
        position: absolute;
        right: 20%;
        bottom: 15%;
        width: 16%;
        border-radius: 15px;
        padding: 30px 0;
    }
    .inner{
        position: relative;
    }
    .fingerprint{
        content: url('../../../../assets/Biometrics/fingerprint.png');
        width: 65%;
        margin:0 auto;
    }
    .logo{
        content: url('../../../../assets/Navbar/blue-logo.png');
        position: absolute;
        bottom:10%;
        right: 10%;
        width: 25%;
    }
}

@media(max-width:1500px){
    .biometrics-page{
        padding-top: 70px;
    }
}
@media(max-width:1401px){
    .biometrics-page .shared-banner{
        clip-path: polygon(0 0, 100% 0, 100% 100%, 100% 100%, 65% 85%, 0 100%);
        h1{
            font-size:32.5px;
        }
        .banner-img{
            right: 20%;
            bottom: 12.5%;
            width: 17%;
            border-radius: 10px;
            padding: 25px 0;
        }
        .fingerprint{
            width: 65%;
        }
        .logo{
            bottom:10%;
            right: 10%;
            width: 28%;
        }
    }
}

@media(max-width:767px){
    .biometrics-page .shared-banner{
        padding:100px 7.5% 35px;
        clip-path: polygon(0 0, 100% 0, 100% 100%, 100% 100%, 65% 90%, 0 100%);
        h1{
            font-size:22.5px;
        }
        .banner-img{
            position: relative;
            right: unset;
            bottom: unset;
            width: 200px;
            max-width: 100%;
            margin:35px auto 0;
            padding: 20px 0;
        }
    }
}