.biometrics-page .biometrics-items .row{
    margin: 7.5% 0;
    padding:0 12.5%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .info{
        width: 39%;
    }
    h1{
        font-size: 35px;
        margin-bottom: 15px;
    }
    p{
        font-size: 19px;
    }
    .images{
        position: relative;
    }
}
.biometrics-page .biometrics-items .row1 .images{
    width: 47.5%;
    .img{
        content: url('../../../../assets/Biometrics/img1.png');
        width: 77.5%;
        position: absolute;
        left:50%;
        top:50%;
        transform: translate(-50%, -50%);
        border-radius: 25px;
    }
    .bg-img{
        content: url('../../../../assets/Biometrics/bg-img1.png');
        width: 100%;
    }
}

.biometrics-page .biometrics-items .row2 .images{
    width: 40%;
    .img{
        content: url('../../../../assets/Biometrics/img2.png');
        width: 60%;
        margin:0 auto;
        position: relative;
        z-index: 2;
        transition: 0.35s ease-in-out;
        &:hover{
            transform: scale(1.035);
        }
    }
    .bg-img{
        content: url('../../../../assets/Biometrics/bg-img2.png');
        width: 82.5%;
        position: absolute;
        left:50%;
        top:50%;
        transform: translate(-50%, -50%);
    }
}

@media(max-width:1401px){
    .biometrics-page .biometrics-items .row{
        margin: 7.5% 0;
        padding:0 10%;
        .info{
            width: 40%;
        }
        h1{
            font-size: 25px;
            margin-bottom: 15px;
        }
        p{
            font-size: 16px;
        }
    }
    .biometrics-page .biometrics-items .row1 .images{
        width: 47.5%;
        .img{
            width: 77.5%;
            border-radius: 20px;
        }
    }
}

@media(max-width:991px){
    @media(max-width:1401px){
        .biometrics-page .biometrics-items .row{
            padding:0 7.5%;
            .info{
                width: 42.5%;
            }
        }
    }
}

@media(max-width:720px){
    .biometrics-page .biometrics-items .row{
        margin: 20px 0;
        flex-wrap: wrap;
        .info{
            width: 100%;
            margin:25px 0;
        }
        h1{
            font-size: 20px;
            margin-bottom: 15px;
        }
        p{
            font-size: 15px;
        }
    }
    .biometrics-page .biometrics-items .row1 .images{
        width: 100%;
        margin:20px 0;
        .img{
            width: 77.5%;
            border-radius: 25px;
        }
    }
    
    .biometrics-page .biometrics-items .row2 .images{
        width: 100%;
        margin:20px 0;
        .img{
            width: 60%;
        }
        .bg-img{
            width: 82.5%;
        }
    }
}